<template>
  <div class="fbasend">
    <el-card>
      <el-row>
        <el-col :span="2">
          <div class="shopname">请选择导入的模块</div>
        </el-col>
        <el-col :span="2">
          <div class="left">
            <el-checkbox-group v-model="checkboxaccount" size="mini">
              <el-checkbox-button>amazon</el-checkbox-button>
            </el-checkbox-group>
          </div>
        </el-col>
      </el-row>

       <el-row>
        <el-col :span="2"><div class="grid-content bg-purple right">请选择导入店铺</div></el-col>
        <el-col :span="22"><div class="grid-content bg-purple-light left">
          
              <el-checkbox-group v-model="checkboxshop" size="mini" >
              <el-checkbox-button  :label="0"   @change="changeshop(0)" :key="0">全部</el-checkbox-button>
              <el-checkbox-button v-for="Shop in storeList" :label="Shop.id"  @change="changeshop(Shop.id)" :key="Shop.id">{{Shop.store_name}}</el-checkbox-button>
            </el-checkbox-group>
            
          </div>
          </el-col>
      </el-row>
     <el-button type="primary" size="mini" @click="uploadFile">
      导入订单文件</el-button>
    </el-card>
      <!-- 添加图片信息开始 -->
      <el-dialog title="添加附件" :visible.sync="dialogImageVisible" center @close="cancelAdd">
        <uploadFileCom :uploadFileData="uploadFileConOne" />
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="cancelAdd">取 消</el-button>
          <el-button size="small" type="primary" @click="addImgList">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 添加图片信息结束 -->
  </div>
</template>
<script>
 import uploadFileCom from '@/components/uploadFile'
export default {
  data() {
    return {
      checkboxaccount:true,
       checkboxshop:[0],
         dialogImageVisible: false,//添加附件弹出框
          uploadFileConOne: {
        limit: 1,
        uploadSrc: '/v1/fbaUplad',
        multiple: false,
        buttonState: false,
        imgList: [],
        comName: 'fileCom'
      },
        uploadFileData: '',//附件信息
        amazonExportTemInfoForm:{
        store_name:'',  //所属店铺
        },
        fileList:[],
         storeList:[],//店铺列表
    }
  },
   components: {
    uploadFileCom
  },
  methods: {
    //点击显示上传附件
     uploadFile(){
      this.dialogImageVisible=true;
     },
      //取消上传
     cancelAdd(){
       this.dialogImageVisible = false;
       this.$store.commit('CHNAGE_FILE_DATA','');
     },
     //上传附件
     addImgList(){
       let formData = new FormData();
       formData.append('attachment',this.uploadFileData[0].raw);
       this.imgRequest(`/v1/fbaUplad`,formData)
       .then(res => {
         if(res.data.code=='200'){
           this.$message({
             type: 'success',
             message: '上传成功'
           })
           this.cancelAdd();
         }
       })
     },
      changeshop(val){
       if(val==0)
       {
         this.checkboxshop=[0];
       }
       else{
         if(this.checkboxshop.indexOf(0)!=-1){
            this.checkboxshop.splice(this.checkboxshop.indexOf(0),1);
         }
       }
       if(this.checkboxshop.length==0)
       {
          this.checkboxshop=[0];
       }
      //  console.log(this.checkboxshop);
        this.$store.state.shopManagementCate.store_id=this.checkboxshop;
            },
     // 获取店铺列表
    async getStoreList() {
      const res = await this.api.stoLists()
      if(res.data.code == 200) {
        this.storeList = res.data.data
        this.amazonExportTemInfoForm.store_name = this.storeList[0].id
      }
    },
  },
  computed:{
 fileData(){
      return this.$store.state.fileData;
    }
  },
  watch:{
 fileData: function(newVal,oldVal){
      this.uploadFileData = newVal;
    }
  },
  created(){
      this.getStoreList()
  }
}
</script>
<style lang="scss" scoped>
.fbasend {
  width: 95%;
  margin-left: 20px;
  background-color: #fff;
  .el-row {
    padding: 10px;
    line-height: 20px;
  }
  .el-checkbox-group{
    text-align: left;
  }
}
</style>
